.itemList {
    background-color: rgba(64, 200, 200, 0.4);
    color: turquoise;
    width: 100%;
    margin: 1rem;
    padding: 0;
    border: 1px solid turquoise;
    border-radius: 1rem;
    box-sizing: border-box;
    display: block;
}

.loading {
    padding: 1rem;
}

ul {
    list-style-type: none;
    text-align: left;
    padding: 0;
    margin: 0;
}

ul li {
    padding: 0.5rem 1rem;
    border-radius: 1rem;
}

li:hover {
    background-color: rgba(64, 200, 200, 0.8);
    cursor: pointer;
}